import React, {Component} from 'react';
import { Container, Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
// import logo from '../Images/l.PNG';
// import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Link } from "react-router-dom";
import { LinearProgress } from '@material-ui/core';
import Logo from '../Images/E.png';
import Image from 'material-ui-image';
const styles = {
  root: {
    flexGrow: 10
  },
 
};

  class Navbar extends Component {
      render() {
        const { classes } = this.props;
              return (
          <div className="Navbar"  >
     <div className={classes.root}>
          <AppBar position="fixed"  color="" top='0' style={{backgroundColor:'rgb(43, 30, 30)'}}>
         
            <Toolbar>
                 <Container>
                      <Row>
                        <Col lg={3} md={3} sm={4} xs={4}>
                          {/* <h1 style={{color:'red',fontWeight:"bolder",fontFamily:''}}><i>E-Shop</i></h1> */}
                        {/* <image scr="data:image/jpeg;base64,"/>
                        */}
                        
                        <img src={`${Logo}`} style={{float:"left"}} />
                        <p className="Ecom">Com-Shop </p>
                        </Col>
                        <Col lg={9} md={9} sm={8} xs={8} >
                   
                        <Link to='/Admin_Login' className='Nav'><i class="fa fa-user-o" aria-hidden="true"></i></Link> 
                        <Link to='/Cards' className='Nav'style={{float:"right"}}>My Cart</Link> 
                        <Link to='/' className='Nav' style={{float:"right"}}>Home</Link>
                        </Col>
                      </Row>
                    </Container>          
            </Toolbar>
          </AppBar>
        </div>
      <br/>
    </div>
      ); 
    }
  }
Navbar.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Navbar);